.sze {
    -webkit-animation: sze 5s ease infinite;
            animation: sze 5s ease infinite;
}
@-webkit-keyframes sze {
    0% {
        transform: scale(1);
    }
    5% {
        transform: scale(1.35);
    }
    20% {
        transform: scale(1);
    }
    30% {
        transform: scale(1.35);
    }
    35% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.35);
    }
    55% {
        transform: scale(1);
    }
    70% {
        transform: scale(1.35);
    }
}
@keyframes sze {
    0% {
        transform: scale(1);
    }
    5% {
        transform: scale(1.35);
    }
