* {
    box-sizing: border-box;
    margin-top: 20px;
  }
  
  body {
    font-family: Arial, Helvetica, sans-serif;
  }
  
  /* Float four columns side by side */
  .News_column__GS_Sj {
    float: left;
    width: 25%;
    padding: 0 10px;
  }
  
  /* Remove extra left and right margins, due to padding in columns */
  .News_row__X1hyO {margin: 0 -5px;}

  .News_row__X1hyO h1 {
      padding-top: 10px;
      font-size: 35px;
      font-weight:800;
      font-family: cursive;
      text-align: center;
  }
  /* Clear floats after the columns */
  .News_row__X1hyO:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Style the counter cards */
  .News_card__3FdXj {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* this adds the "card" effect */
    padding: 16px;
    text-align: center;
    background-color: #f1f1f1;
  }
  .News_card__3FdXj p{
    width: 300px;
    height:80px;
    font-size: 20px;
    word-wrap: break-word;
  }
  .News_card__3FdXj button{
    border: none;
    outline: 0;
    padding: 12px;
    color: white;
    background-color: rgb(0, 0, 0);
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
  }
  .News_card__3FdXj a{
      color: white;
      text-decoration: none;
  }
  
  .News_card__3FdXj button:hover {
    opacity: 0.7;
  }
  .News_image__1FDkt{
      width:100%;
      height:200px;
  }
  /* Responsive columns - one column layout (vertical) on small screens */
  @media screen and (max-width: 750px) {
    .News_column__GS_Sj {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }
  
